import './scss/styles.scss';
import { API_URL, CDN_URL } from './utils/constants';
import { LarekApi } from './components/LarekApi';
import { EventEmitter } from './components/base/events';
import { cloneTemplate, ensureElement } from './utils/utils';
import { AppState } from './components/AppData';
import { Page } from './components/Page';
import { Card, BasketItem } from './components/Card';
import { Modal } from './components/common/Modal';
import { Basket } from './components/common/Basket';
import { OrderContacts } from './components/OrderContacts';
import { OrderPayments } from './components/OrderPayments';
import { Success } from './components/common/Success';
const events = new EventEmitter();
const api = new LarekApi(CDN_URL, API_URL);
// Шаблоны
const cardCatalogTemplate = ensureElement('#card-catalog');
const cardPreviewTemplate = ensureElement('#card-preview');
const basketTemplate = ensureElement('#basket');
const cardBasketTemplate = ensureElement('#card-basket');
const OrderPaymentsTemplate = ensureElement('#order');
const OrderContactsTemplate = ensureElement('#contacts');
const successTemplate = ensureElement('#success');
// Модель данных приложения
const appData = new AppState({}, events);
// Глобальные контейнеры
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
const basket = new Basket(cloneTemplate(basketTemplate), events);
const orderPayments = new OrderPayments(cloneTemplate(OrderPaymentsTemplate), events);
const orderContacts = new OrderContacts(cloneTemplate(OrderContactsTemplate), events);
// Изменились элементы каталога
events.on('cards:changed', () => {
    page.catalog = appData.catalog.map((item) => {
        const card = new Card(cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('card:select', item),
        });
        return card.render({
            id: item.id,
            title: item.title,
            image: item.image,
            category: item.category,
            price: item.price,
        });
    });
});
// Отправить в превью карточку
events.on('card:select', (item) => {
    appData.setPreview(item);
});
// Открылось превью карточки товара
events.on('preview:changed', (item) => {
    const card = new Card(cloneTemplate(cardPreviewTemplate), {
        onClick: () => {
            events.emit('card:toBasket', item);
            events.emit('preview:changed', item);
        },
    });
    modal.render({
        content: card.render({
            title: item.title,
            image: item.image,
            description: item.description,
            price: item.price,
            category: item.category,
            buttonName: appData.setButtonText(item),
        }),
    });
});
// Изменились товары в корзине
events.on('basket:changed', () => {
    page.counter = appData.getCardsInBasket().length;
    basket.items = appData.getCardsInBasket().map((item) => {
        const basketItem = new BasketItem(cloneTemplate(cardBasketTemplate), {
            onClick: () => {
                events.emit('card:fromBasket', item);
            },
        });
        basketItem.index = appData.getBasketItemIndex(item);
        return basketItem.render({
            title: item.title,
            price: item.price,
        });
    });
    basket.total = appData.getTotal();
});
// Добавить в корзину
events.on('card:toBasket', (item) => {
    appData.toggleCardInBasket(item);
});
// Удалить из корзины
events.on('card:fromBasket', (item) => {
    appData.deleteCardFromBasket(item);
});
// Открыть корзину
events.on('basket:open', () => {
    modal.render({
        content: basket.render(),
    });
});
// Открыть форму заказа
events.on('order:open', () => {
    orderPayments.cancelPayment();
    modal.render({
        content: orderPayments.render({
            address: '',
            valid: false,
            errors: [],
        }),
    });
});
// Переключение вида оплаты товара
events.on('order:change payment', (data) => {
    appData.setOrderPayment(data.payment);
    orderPayments.togglePayment(data.button);
    appData.validateOrder();
});
// Открытие формы контактов заказа
events.on('order:submit', () => {
    modal.render({
        content: orderContacts.render({
            email: '',
            phone: '',
            valid: false,
            errors: [],
        }),
    });
});
// Отправлена форма заказа
events.on('contacts:submit', () => {
    appData.sendCardsInOrder();
    api
        .orderProducts(appData.order)
        .then((result) => {
        const success = new Success(cloneTemplate(successTemplate), {
            onClick: () => {
                modal.close();
            },
        });
        modal.render({
            content: success.render({
                total: result.total,
            }),
        });
        appData.clearBasket();
    })
        .catch((err) => {
        console.error(err);
    });
});
// Изменилось одно из полей input
events.on(/^order\..*:change/, (data) => {
    appData.setOrderField(data.field, data.value);
});
// Изменилось состояние валидации формы
events.on('formErrors:change', (errors) => {
    const { address, payment, phone, email } = errors;
    orderPayments.valid = !payment && !address;
    orderPayments.errors = Object.values({ payment, address })
        .filter((i) => !!i)
        .join('; ');
    orderContacts.valid = !phone && !email;
    orderContacts.errors = Object.values({ phone, email })
        .filter((i) => !!i)
        .join('; ');
});
events.on('modal:open', () => {
    page.locked = true;
});
events.on('modal:close', () => {
    page.locked = false;
});
api
    .getCardList()
    .then(appData.setCatalog.bind(appData))
    .catch((err) => {
    console.error(err);
});
