import { Form } from './common/Form';
export class OrderPayments extends Form {
    constructor(container, events) {
        super(container, events);
        this._cardButton = this.container.elements.namedItem('card');
        this._cashButton = this.container.elements.namedItem('cash');
        if (this._cardButton) {
            this._cardButton.addEventListener('click', () => {
                events.emit(`order:change payment`, {
                    payment: this._cardButton.name,
                    button: this._cardButton,
                });
            });
        }
        if (this._cashButton) {
            this._cashButton.addEventListener('click', () => {
                events.emit(`order:change payment`, {
                    payment: this._cashButton.name,
                    button: this._cashButton,
                });
            });
        }
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    togglePayment(value) {
        this.cancelPayment();
        this.toggleClass(value, 'button_alt-active', true);
    }
    cancelPayment() {
        this.toggleClass(this._cardButton, 'button_alt-active', false);
        this.toggleClass(this._cashButton, 'button_alt-active', false);
    }
}
