import { Model } from './base/Model';
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.catalog = [];
        this.basket = [];
        this.order = {
            email: '',
            phone: '',
            payment: '',
            address: '',
            total: 0,
            items: [],
        };
        this.formErrors = {};
    }
    setOrderPayment(value) {
        if (this.order.payment !== value)
            this.order.payment = value;
    }
    setOrderAddress(value) {
        this.order.address = value;
    }
    setCatalog(items) {
        items.map((item) => (this.catalog = [...this.catalog, item]));
        this.emitChanges('cards:changed', { catalog: this.catalog });
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    setButtonText(item) {
        if (this.basket.some((card) => card.id === item.id)) {
            return 'Убрать';
        }
        else
            return 'В корзину';
    }
    getCardsInBasket() {
        return this.basket;
    }
    getBasketItemIndex(item) {
        return this.basket.indexOf(item) + 1;
    }
    toggleCardInBasket(item) {
        if (!this.basket.some((card) => card.id === item.id)) {
            this.basket = [...this.basket, item];
            this.emitChanges('basket:changed');
        }
        else {
            this.deleteCardFromBasket(item);
        }
    }
    deleteCardFromBasket(item) {
        if (this.basket.some((card) => card.id === item.id)) {
            this.basket = this.basket.filter((card) => item.id !== card.id);
            this.emitChanges('basket:changed');
        }
        return;
    }
    sendCardsInOrder() {
        this.basket.forEach((card) => (this.order.items = [...this.order.items, card.id]));
        this.order.total = this.getTotal();
    }
    clearBasket() {
        this.order = {
            email: '',
            phone: '',
            payment: '',
            address: '',
            total: 0,
            items: [],
        };
        this.basket = [];
        this.emitChanges('basket:changed');
    }
    getTotal() {
        let total = 0;
        this.basket.forEach((card) => (total += card.price));
        return total;
    }
    setOrderField(field, value) {
        this.order[field] = value;
        this.validateOrder();
    }
    validateOrder() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this.order.phone) {
            errors.phone = 'Необходимо указать телефон';
        }
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес доставки';
        }
        if (!this.order.payment) {
            errors.payment = 'Необходимо указать способ оплаты';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
